import React, { Fragment, useEffect, useState, useRef } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, RefreshCw
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import useJwt from '@src/auth/jwt/useJwt'
import { toast } from 'react-toastify'
import { useHistory } from 'react-router-dom'
import { PHONE_PREFIX } from '../../../Configuarables'
import { Error, Success, ErrorMessage } from '../../../viewhelper'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)

const CreateNewBranch = () => {
    const history = useHistory()
    const managerRef = useRef()
    const [isLoading, setLoading] = useState(false)
    const [managerList, setmanagerList] = useState([])
    const [userInput, setUserInput] = useState({
        branch_name: '',
        sub_estate_manager_id: ''
    })
    useEffect(() => {
        useJwt.EstateManagerList().then(res => {
            console.log(res.data.payload)
            setmanagerList(res.data.payload)
        }).catch(err => {
            Error(err)
            console.log(err)
        })
    }, [])
    const handleChange = e => {
        setUserInput({ ...userInput, [e.target.name]: e.target.value })
    }
    const handleSubmit = e => {
        e.preventDefault()
        console.log(userInput)
        setLoading(true)
        const { branch_name, sub_estate_manager_id } = userInput
        useJwt.createBranch({ branch_name, sub_estate_manager_id }).then(res => {
            console.log(res)
            Success(res)
            setLoading(false)
            history.push('/EstateAdmin/MyBranches')
        }).catch(err => {
            Error(err)
            setLoading(false)
        })
    }
    return (
        <Fragment>
            <Card>
                <CardHeader className='border-bottom'>
                    <CardTitle tag='h4'>Create New Branch</CardTitle>
                </CardHeader>
                <CardBody>
                    <Form className="row pt-1" style={{ width: '100%' }} onSubmit={handleSubmit} autoComplete="off">
                        <Col md='4' >
                            <FormGroup>
                                <Label for="branch_name">Branch Name</Label>
                                <Input
                                    required
                                    type="text"
                                    id='branch_name'
                                    name="branch_name"
                                    placeholder="name"
                                    value={userInput.branch_name}
                                    onChange={handleChange}
                                />
                            </FormGroup>
                        </Col>
                        <Col md='4' >
                            <Label>Branch Manager</Label>
                            <Select
                                ref={managerRef}
                                theme={selectThemeColors}
                                className='react-select'
                                classNamePrefix='select'
                                name='sub_estate_manager_id'
                                onChange={selected => {
                                    setUserInput({ ...userInput, sub_estate_manager_id: selected.value })
                                }}
                                options={managerList.map(x => { return { value: x.user_id, label: x.full_name } })}
                                // value={{ value: userInput.sub_estate_manager_id, label: userInput.sub_estate_manager_id }}
                                isLoading={!managerList.length}
                                isClearable={false}
                            />
                            <Input
                                required
                                style={{
                                    opacity: 0,
                                    width: "100%",
                                    height: 0
                                    // position: "absolute"
                                }}
                                onFocus={e => managerRef.current.select.focus()}
                                value={userInput.sub_estate_manager_id || ''}
                                onChange={e => ''}
                            />
                        </Col>
                        <Col md="4 text-center">
                            <Button.Ripple className='mt-2' color='primary' type="submit" disabled={isLoading}>
                                {isLoading ? <Spinner size='sm' /> : 'Submit'}
                            </Button.Ripple>
                        </Col>
                    </Form>
                </CardBody>
            </Card>
        </Fragment>
    )
}

export default CreateNewBranch